import {AppInfoView, AppSearchBar, AppsPagination} from '@crema';
import AppsContent from '@crema/core/AppsContainer/AppsContent';
import AppsHeader from '@crema/core/AppsContainer/AppsHeader';
import {Box, Button, Hidden, Modal} from '@mui/material';
import CopounGenFrom from 'components/CopounGenFrom/CopounGenFrom';
import {useEffect, useState} from 'react';
import {useIntl} from 'react-intl';
import {useDispatch, useSelector} from 'react-redux';
// import { useNavigate } from 'react-router-dom';
import {getCoupn} from 'redux/actions';

import AppsContainer from '../../../@crema/core/AppsContainer';
import CouponTable from './CopounTable';

const Copoun = () => {
  const {messages} = useIntl();
  const dispatch = useDispatch();
  const coupon = useSelector(({ecommerce}) => ecommerce.coupon);
  const couponCount = useSelector(({ecommerce}) => ecommerce.couponCount);
  const lastVisible = useSelector(({ecommerce}) => ecommerce.lastVisible);
  const [index, setIndex] = useState('');
  // const navigation = useNavigate();
  const [openVariantModal, setOpenVariantModal] = useState(false);
  console.log(coupon);
  const [page, setPage] = useState(0);
  const [search, setSearchQuery] = useState('');
  const [isNext, setIsNext] = useState('');
  // const employees = () => {
  //   navigation('/ecommerce/employees/AddEmployee');
  // };
  const onPageChange = (event, value) => {
    if (value > page) {
      setIsNext(true);
    } else {
      setIsNext(false);
    }
    setIndex(lastVisible);
    setPage(value);
  };
  useEffect(() => {
    dispatch(getCoupn(search, page, index, isNext));
  }, [dispatch, search, page, index]);

  const onSearchCoupn = (e) => {
    setSearchQuery(e);
    setPage(0);
  };

  const handleCloseVariantModal = () => {
    setOpenVariantModal(false);
  };
  return (
    <>
      <AppsContainer title={messages['sidebar.app.dashboard.copoun']} fullView>
        <AppsHeader>
          <Box
            display='flex'
            flexDirection='row'
            alignItems='center'
            width={1}
            justifyContent='space-between'
          >
            <AppSearchBar
              iconPosition='right'
              overlap={false}
              onChange={(event) => onSearchCoupn(event.target.value)}
              placeholder={messages['common.searchHere']}
            />
            <Box display='flex' flexDirection='row' alignItems='center'>
              <Button
                variant='contained'
                color='primary'
                onClick={() => setOpenVariantModal(true)}
              >
                {messages['sidebar.app.dashboard.addcopoun']}
              </Button>

              <Hidden smDown>
                <AppsPagination
                             rowsPerPage={40}

                  count={couponCount}
                  page={page}
                  onPageChange={onPageChange}
                />
              </Hidden>
            </Box>
          </Box>
        </AppsHeader>

        <AppsContent
          sx={{
            paddingTop: 2.5,
            paddingBottom: 2.5,
          }}
        >
          {<CouponTable couponData={coupon} />}
        </AppsContent>

        <Hidden smUp>
          <AppsPagination
                       rowsPerPage={40}

            count={couponCount}
            page={page}
            onPageChange={onPageChange}
          />
        </Hidden>
      </AppsContainer>
      <AppInfoView />
      <Modal
        aria-labelledby='transition-modal-title'
        aria-describedby='transition-modal-description'
        open={openVariantModal}
        onClose={handleCloseVariantModal}
        closeAfterTransition
      >
        <CopounGenFrom />
      </Modal>
    </>
  );
};

export default Copoun;
