import React from 'react';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Fade from '@mui/material/Fade';
import IconButton from '@mui/material/IconButton';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import Box from '@mui/material/Box';
// import { useNavigate } from 'react-router-dom';
import PropTypes from 'prop-types';
import {deletCoupne} from '@crema/services/auth/firebase/firebase';

const CoupnActions = ({data}) => {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  // const navigation = useNavigate();
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  const handleViewOrder = () => {
    setAnchorEl(null);
    // navigation('/management/orderDetials/:'+data.orderId, {state:data}  );

    console.log(data);
  };
  const handleDelete = (e) => {
    e.preventDefault();
    let wantDelet = confirm('Are you sure to Dele this Coupn');
    if (wantDelet) {
      deletCoupne(data);
    }
  };
  return (
    <Box>
      <IconButton
        aria-controls='alpha-menu'
        aria-haspopup='true'
        onClick={handleClick}
      >
        <MoreVertIcon />
      </IconButton>
      <Menu
        id='alpha-menu'
        anchorEl={anchorEl}
        keepMounted
        open={open}
        onClose={handleClose}
        TransitionComponent={Fade}
      >
        <MenuItem style={{fontSize: 14}} onClick={handleViewOrder}>
          View Order
        </MenuItem>
        <MenuItem style={{fontSize: 14}} onClick={handleClose}>
          Edit
        </MenuItem>
        <MenuItem style={{fontSize: 14}} onClick={(e) => handleDelete(e)}>
          Delete
        </MenuItem>
      </Menu>
    </Box>
  );
};

CoupnActions.propTypes = {
  data: PropTypes.object.isRequired,
};
export default CoupnActions;
